document.addEventListener('DOMContentLoaded', function(){
    gallery();

    selectedImg();


});

function gallery(){
    const galeria = document.querySelector('.galeria-imagenes');

    for( let i = 1; i <=15; i++){
        const imagen = document.createElement('IMG');
        imagen.src = `build/gallery/${i}.jpg`;
        imagen.classList.add('elemento-imagen')
        //Darle un Id set en HTML 5 para la imagen
        imagen.dataset.imagenId = i;

         const lista = document.createElement('DIV');
         lista.classList.add('imagen');
         lista.appendChild(imagen);

         galeria.appendChild(lista);
    }
}

function selectedImg(){
    let imgs = document.querySelectorAll('.imagen');

    imgs.forEach(img => {
        img.addEventListener('click', ()=>{
            if(img.classList.contains('selected')){
                img.classList.remove('selected');
                removeImg();
            } else {
                img.classList.add('selected');
                addImg();
            }
        });
    });
}

function addImg(){
    console.log('se agrego img a object')
}

function removeImg(){
    console.log('se elimina imagen del object')
}